.wk-slideshow {
    visibility: hidden;
    .next, .prev {
        position: absolute;
        z-index: 5;
        cursor: pointer;
    }
    .caption {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 5;
        padding: 10px;
        background: rgba(0, 0, 0, 0.5);
        color: #fff;
    }
