    }
}

#lightbox-loading {
    display: none;
    position: fixed;
    top: 50%;
    left: 50%;
    width: 40px;
    height: 40px;
    margin: -20px 0 0 -20px;
    cursor: pointer;
    overflow: hidden;
    z-index: 1104;
    div {
        position: absolute;
        top: 0;
        left: 0;
        width: 40px;
        height: 480px;
        background-image: url(/media/widgetkit/widgets/lightbox/images/loading.png);
    }
}

#lightbox-overlay {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1100;
}

#lightbox-tmp {
    display: none;
    padding: 0;
    margin: 0;
    border: 0;
    overflow: auto;
}

#lightbox-wrap {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    padding: 20px;
    z-index: 1101;
    outline: 0;
}

#lightbox-outer {
    position: relative;
    width: 100%;
    height: 100%;
    background: #fff;
    box-shadow: 0 0 5px #000;
    -webkit-box-shadow: 0 0 5px #000;
    -moz-box-shadow: 0 0 5px #000;
}

#lightbox-content {
    position: relative;
    width: 0;
    height: 0;
    padding: 0;
    outline: #fff solid 0;
    overflow: hidden;
    z-index: 1102;
}

#lightbox-close {
    display: none;
    position: absolute;
    top: -17px;
    right: -17px;
    width: 34px;
    height: 34px;
    background: url(/media/widgetkit/widgets/lightbox/images/close.png) no-repeat;
    cursor: pointer;
    z-index: 1103;
}

#lightbox-left, #lightbox-right {
    display: none;
    position: absolute;
    top: 100px;
    bottom: 100px;
    width: 35%;
    outline: 0;
    background: url(/media/widgetkit/widgets/lightbox/images/blank.gif);
    z-index: 1102;
    cursor: pointer;
}

#lightbox-left {
    left: 0;
}

#lightbox-right {
    right: 0;
}

#lightbox-left:hover span {
    left: 30px;
}

#lightbox-right:hover span {
    left: auto;
    right: 30px;
}

#lightbox-title {
    font-size: 13px;
    z-index: 1102;
    -webkit-animation-name: uk-slide-top;
    animation-name: uk-slide-top;
