    background-position: left -1000px;
}

.extravote-star a, .extravote-stars .current-rating {
    border: none;
    cursor: pointer;
    left: 0;
    outline: 0;
    overflow: hidden;
    position: absolute;
    text-indent: (-1000em) !important;
    top: 0;
}

.extravote-star a {
    &:hover {
        background-position: left bottom !important;
    }
    &:active, &:focus {
        background: 0 0 !important;
